* {
  box-sizing: border-box ;
  margin: 0 ;
  padding: 0 ;
  font-family: 'Rubik', sans-serif;
}

.app .leaflet-container.leaflet-touch-drag.leaflet-touch-zoom {
  height: calc(100vh - 250px ) ;
}
.app .MuiAlert-filledError {
  min-width: 350px ;
}

.app {
  height: 100vh ;
  background-color: #252934 ;
}
.app .header .MuiButton-root {
  padding: 6px;
  font-size: 15px;
}

.app .header .MuiIconButton-root {
  padding: 6px ;
}

html {
  font-size: 17px ;
}


@media screen and (max-width: 700px) {
  html {
    font-size: 13px ;
  }
  .app .MuiAlert-filledError {
    min-width: 80vw ;
    max-width: 80vw ;
  }
  .app .header .MuiButton-root {
    padding: 3px;
    font-size: 13px;
  }
  .app .header .MuiAlert-action {
    padding-left: 3px ;
  }
  .app .header .MuiAlert-icon {
    display: flex;
    align-items: center;
  }
}

